  .boton-amarillo:hover {
    background-color: #fef144; }

.boton-amarillo-block {
  background-color: #FEF46D;
  color: #FFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1.5rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  min-width: 15rem; }
  .boton-amarillo-block:hover {
