.footer {
  background-color: #332F2C; }
  .footer .copyright {
    color: #828282;
    text-align: center;
    padding: 1.25rem; }

.header {
  background-color: #332F2C;
  padding: 1.5rem; }

.barra {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem; }
  @media (min-width: 768px) {
    .barra {
      gap: 2rem; } }

.logo {
  width: 5rem; }

.navegacion {
  grid-column: 1/3;
  grid-row: 2; }
  @media (min-width: 768px) {
    .navegacion {
      grid-column: 2/3;
      grid-row: 1;
      display: flex;
      justify-items: center;
      align-items: center;
      justify-content: flex-end; } }
  .navegacion .links {
    display: none;
    flex-direction: column;
    align-items: center; }
    @media (min-width: 768px) {
      .navegacion .links {
        display: flex;
        flex-direction: row;
        align-items: center; } }
    .navegacion .links a {
      color: #FFF;
      font-size: 2.2rem;
      display: block;
      text-align: center;
      margin-top: 1.5rem; }
      @media (min-width: 768px) {
        .navegacion .links a {
          margin-right: 2rem;
          margin-top: 0; }
          .navegacion .links a:last-of-type {
            margin-right: 0; } }
      .navegacion .links a:hover {
        color: #4CB8B3; }
  .navegacion .links__show {
    display: flex; }
  .navegacion .links__hide {
    display: none; }

.btn_menu {
  display: flex;
  justify-content: flex-end;
  color: #FFF; }
  .btn_menu:hover {
    color: #4CB8B3; }
  @media (min-width: 768px) {
    .btn_menu {
      display: none; } }

.inicio {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .inicio {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 5rem; } }

.presentacion {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .presentacion .presentacion_title {
    color: black; }
  .presentacion .presentacion_desc {
    color: #332F2C;
